import { distance, linePointToLine } from '@antv/g-math';

export function inBox(
  minX: number,
  minY: number,
  width: number,
  height: number,
  x: number,
  y: number,
) {
  return x >= minX && x <= minX + width && y >= minY && y <= minY + height;
}

export function inRect(
  minX: number,
  minY: number,
  width: number,
  height: number,
  lineWidth: number,
  x: number,
  y: number,
) {
  const halfWidth = lineWidth / 2;
  // 
  return (
    inBox(minX - halfWidth, minY - halfWidth, width, lineWidth, x, y) || // 
    inBox(
      minX + width - halfWidth,
      minY - halfWidth,
      lineWidth,
      height,
      x,
      y,
    ) || // 
    inBox(
      minX + halfWidth,
      minY + height - halfWidth,
      width,
      lineWidth,
      x,
      y,
    ) || // 
    inBox(minX - halfWidth, minY + halfWidth, lineWidth, height, x, y)
  ); // 
}

export function inArc(
  cx: number,
  cy: number,
  r: number,
  startAngle: number,
  endAngle: number,
  lineWidth: number,
  x: number,
  y: number,
) {
  const angle = (Math.atan2(y - cy, x - cx) + Math.PI * 2) % (Math.PI * 2); //  0 - 2 * Math.PI 
  // if (angle < startAngle || angle > endAngle) {
  //   return false;
  // }
  const point = {
    x: cx + r * Math.cos(angle),
    y: cy + r * Math.sin(angle),
  };

  return distance(point.x, point.y, x, y) <= lineWidth / 2;
}

export function inLine(
  x1: number,
  y1: number,
  x2: number,
  y2: number,
  lineWidth: number,
  x: number,
  y: number,
) {
  const minX = Math.min(x1, x2);
  const maxX = Math.max(x1, x2);
  const minY = Math.min(y1, y2);
  const maxY = Math.max(y1, y2);
  const halfWidth = lineWidth / 2;
  // 
  //  lineWidth 
  if (
    !(
      x >= minX - halfWidth &&
      x <= maxX + halfWidth &&
      y >= minY - halfWidth &&
      y <= maxY + halfWidth
    )
  ) {
    return false;
  }
  // 
  return linePointToLine(x1, y1, x2, y2, x, y) <= lineWidth / 2;
}

export function inPolyline(
  points: any[],
  lineWidth: number,
  x: number,
  y: number,
  isClose: boolean,
) {
  const count = points.length;
  if (count < 2) {
    return false;
  }
  for (let i = 0; i < count - 1; i++) {
    const x1 = points[i][0];
    const y1 = points[i][1];
    const x2 = points[i + 1][0];
    const y2 = points[i + 1][1];

    if (inLine(x1, y1, x2, y2, lineWidth, x, y)) {
      return true;
    }
  }

  // 
  if (isClose) {
    const first = points[0];
    const last = points[count - 1];
    if (inLine(first[0], first[1], last[0], last[1], lineWidth, x, y)) {
      return true;
    }
  }

  return false;
}

// https://blog.csdn.net/WilliamSun0122/article/details/77994526
const tolerance = 1e-6;
// doubleeps
function dcmp(x: number) {
  if (Math.abs(x) < tolerance) {
    return 0;
  }

  return x < 0 ? -1 : 1;
}

// Qp1p2
function onSegment(p1: any, p2: any, q: any) {
  if (
    (q[0] - p1[0]) * (p2[1] - p1[1]) === (p2[0] - p1[0]) * (q[1] - p1[1]) &&
    Math.min(p1[0], p2[0]) <= q[0] &&
    q[0] <= Math.max(p1[0], p2[0]) &&
    Math.min(p1[1], p2[1]) <= q[1] &&
    q[1] <= Math.max(p1[1], p2[1])
  ) {
    return true;
  }
  return false;
}

// P-
export function inPolygon(points: any[], x: number, y: number) {
  let isHit = false;
  const n = points.length;
  if (n <= 2) {
    // svg  3 
    return false;
  }
  for (let i = 0; i < n; i++) {
    const p1 = points[i];
    const p2 = points[(i + 1) % n];
    if (onSegment(p1, p2, [x, y])) {
      // 
      return true;
    }
    // min(p1[1],p2[1])<P.y<=max(p1[1],p2[1])
    //    
    if (
      dcmp(p1[1] - y) > 0 !== dcmp(p2[1] - y) > 0 &&
      dcmp(x - ((y - p1[1]) * (p1[0] - p2[0])) / (p1[1] - p2[1]) - p1[0]) < 0
    ) {
      isHit = !isHit;
    }
  }
  return isHit;
}
export function inPolygons(polygons: any[], x: number, y: number): boolean {
  let isHit = false;
  for (let i = 0; i < polygons.length; i++) {
    const points = polygons[i];
    isHit = inPolygon(points, x, y);
    if (isHit) {
      break;
    }
  }
  return isHit;
}
